.enjoy {
    -webkit-animation: example 5s infinite; /* Safari 4.0 - 8.0 */
    animation: example 5s infinite;
}

.talk {
  display: inline-block;
  width: 200px;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  border-radius: 10px;
  background-color: #fff;
  background: -webkit-linear-gradient(right, #F46737 0%, #945DD6 100%);
  background: linear-gradient(270deg, #F46737 0%, #945DD6 100%);
  padding-top: 10px;
  padding-left: 35px;
  padding-right: 35px;
  padding-bottom: 10px;
  color: white;
  cursor: pointer;
}

.talk span {
  display: inline-block;
  position: relative;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.talk span:after {
  content: ':)';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.talk:hover span {
  padding-right: 25px;
}

.talk:hover span:after {
  opacity: 1;
  right: 0;
}

  /* Safari 4.0 - 8.0 */
@-webkit-keyframes example {
  from {color: rgb(25, 0, 255);}
  to {color: rgb(21, 128, 12);}
  from {color: rgb(21, 128, 12);}
  to {color: rgb(233, 25, 10);}
  from {color: rgb(233, 25, 10);}
  to {color: rgb(25, 0, 255);}
}

/* Standard syntax */
@keyframes example {
  from {color: rgb(25, 0, 255);}
  to {color: rgb(21, 128, 12);}
  from {color: rgb(21, 128, 12);}
  to {color: rgb(233, 25, 10);}
  from {color: rgb(233, 25, 10);}
  to {color: rgb(25, 0, 255);}
}



